@import url("https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@700&family=Lexend+Deca&display=swap");
@media (max-width: 400px) {
  .navbar-title {
    font-size: 30px;
  }
}

@media (max-width: 350px) {
  .navbar-title {
    font-size: 20px;
  }
}

.navbar {
  width: 100%;
  height: 4.5rem;
  position: absolute;
  background-color: var(--secondary-color);
}

.navbar .navbar-title {
  margin-left: 2rem;
  font-family: 'Big Shoulders Display', sans-serif;
  display: inline-block;
}

ul.social-list {
  list-style: none;
}

.social-icon {
  width: 50px;
  aspect-ratio: 1;
}

.social-list-item {
  display: inline-block;
  position: relative;
  top: -10%;
  padding-right: 25px;
}

div.flex-contain {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 0;
}

@media (max-width: 700px) {
  .social-icon {
    width: 40px;
    aspect-ratio: 1;
  }
  .social-item-list {
    padding-right: 20px;
  }
}

@media (max-width: 400px) {
  .social-icon {
    width: 30px;
    aspect-ratio: 1;
  }
  .social-item-list {
    padding-right: 15px;
  }
}
